  display: flex;
  gap: 20px;
  height: 140px;
}

.description-box {
  flex: 1;
  background-color: #f5f5f5;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 0;
  color: #333;
  overflow-y: auto;
}

.description-box.describing, .description-box.ready {
    background-color: #e9ecef;
    color: #6c757d;
    font-style: italic;
  }

.description-controls {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  gap: 10px;
}

.description-controls button {
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    white-space: nowrap;
  }

.description-controls button:disabled {
      background-color: #cccccc;
      cursor: not-allowed;
    }

.description-controls button:hover:not(:disabled) {
      background-color: #0056b3;
    }

.description-controls button.reset-button {
        background-color: #007bff;
    }

.description-controls button.reset-button:hover:not(:disabled) {
        background-color: #0056b3;
      }

.description-controls button.reset-button:disabled {
        background-color: #cccccc;
      }

#root {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.stage-container {
  flex: 1;
  min-height: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.hover\\:bg-blue-700:hover {
  --tw-bg-opacity: 1;
  background-color: rgba(29, 78, 216, var(--tw-bg-opacity));
}

.hover\\:bg-red-700:hover {
  --tw-bg-opacity: 1;
  background-color: rgba(185, 28, 28, var(--tw-bg-opacity));
}

.hover\\:text-gray-800:hover {
  --tw-text-opacity: 1;
  color: rgba(31, 41, 55, var(--tw-text-opacity));
}

.hover\\:opacity-80:hover {
  opacity: 0.8;
}

@media (min-width: 640px) {

  .sm\\:flex-row {
    flex-direction: row;
  }

  .sm\\:space-x-8 > :not([hidden]) ~ :not([hidden]) {
    --tw-space-x-reverse: 0;
    margin-right: calc(2rem * var(--tw-space-x-reverse));
    margin-left: calc(2rem * (1 - var(--tw-space-x-reverse)));
    margin-left: calc(2rem * calc(1 - var(--tw-space-x-reverse)));
  }

  .sm\\:text-left {
    text-align: left;
  }
}
